import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { setHancurBarang } from "../../../actions/databarang_action";
import { getDataKondisiBarang } from "../../../actions/datamaster_action";
import { ReanderSelect, ReanderField } from "../../../components/helpers/field";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import { getData } from "../../../config/axios";
import ValidasiPindahBarang from "../../../Validasi/Barang/PindahBarang/ValidasiPindahBarang";
const maptostate = (state) => {
  return {
    datakondisi: state.datamaster.getDataKondisiBarang,
    setHancurBarang: state.databarang.setHancurBarang,
  };
};
class HeadaHancurDataBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      isLoadingBatal: false,
      kondisi_barang: "kosong",
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataKondisiBarang());
  }
  getKodeBarcode(e) {
    if(e === ""){
      return false;
    }
    getData("barang/open/kode-barcode/" + e)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Dicari Tidak Ada");
        } else {
          if (this.state.kondisi_barang ==="kosong") {
              ToastNotification("info", "Kondisi Barang Harus Dipilih");
              this.props.change("kode_barcode", "");
          } else {
            res.data.forEach((list, index) => {
              let data = {
                kode_barcode: list.kode_barcode,
                kode_group: list.kode_group,
                kode_dept: list.kode_dept,
                nama_barang: list.nama_barang,
                nama_atribut: list.nama_atribut,
                berat: list.berat,
                kondisi_barang: this.state.kondisi_barang,
              };
              //   console.log(data);
              let cek =
                JSON.parse(localStorage.getItem("data_barang_hancur_tmp")) ||
                [];
              let hasil_cek = cek.findIndex(
                (hasil) => hasil.kode_barcode === e
              );
              //   console.log(index);
              if (hasil_cek === -1) {
                cek.push(data);
                localStorage.setItem(
                  "data_barang_hancur_tmp",
                  JSON.stringify(cek)
                );
                  this.props.dispatch(setHancurBarang(cek));
                 this.props.change("kode_barcode", "");
              }
            });
          }
        }
      })
      .catch((err) => {
        ToastNotification("info", err.response && err.response.data);
      });
  }

  batalHancur() {
    this.setState({ isLoadingBatal: true })
    setTimeout(() => {
      this.setState({ isLoadingBatal: false })
      localStorage.setItem('data_barang_hancur_tmp', JSON.stringify([]))
      this.props.dispatch(setHancurBarang([]));
    }, 500);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off"
      onKeyPress={(e) => {
        e.key === "Enter" && e.preventDefault();
      }}
      >
        <div className="row">
          <div className="col-lg-6">
            <Field
              label="Kondisi Barang"
              name="kondisi_barang"
              onChange={(e) => {
                this.setState({ kondisi_barang: e });
              }}
              options={this.props.datakondisi
                .filter((list) => list.kondisi_barang !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kondisi_barang,
                    name: list.kondisi_barang,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kondisi Barang"}
              component={ReanderSelect}
            />
          </div>

          <div className="col-lg-6">
            <Field
              onChange={(e) => this.getKodeBarcode(e.target.value)}
              name="kode_barcode"
              component={ReanderField}
              type="text"
              label="Kode Barcode"
              placeholder="Masukan Kode Barcode"
            />
          </div>
        </div>
        <Tabel
          keyField="kode_barcode"
          pagination="off"
          exportCsv={false}
          data={
            this.props.setHancurBarang.length === 0
              ? JSON.parse(localStorage.getItem("data_barang_hancur_tmp"))
              : this.props.setHancurBarang
          }
          columns={this.props.columns}
          empty={true}
          textEmpty="Data Barang Kosong"
        />
        <div className="row">
          <div className="col-10">
            <button
              className="btn btn-primary"
              type="submit"
              disabled={this.props.isLoading}
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
            &nbsp;
            <button
              className="btn btn-warning"
              type="button"
              onClick={()=>this.batalHancur()}
              disabled={this.props.isLoading}
            >
              {this.state.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Membatal
                </>
              ) : (
                "Batal"
              )}
            </button>
          </div>
          {/* <div className="2">
            {JSON.parse(localStorage.getItem("data_barang_hancur_tmp")) ===
            null ? null : (
              <>
                Total Barang :{" "}
                {this.props.setHancurBarang.length === 0
                  ? JSON.parse(localStorage.getItem("data_barang_hancur_tmp"))
                      .length || 0
                  : this.props.setHancurBarang.length || 0}
                <br />
                Total Berat :
                {this.props.setHancurBarang.length === 0
                  ? JSON.parse(localStorage.getItem("data_barang_hancur_tmp"))
                      .map((column, key) => column.berat)
                      .reduce((a, b) => a + b, 0)
                      .toFixed(1)
                  : this.props.setHancurBarang
                      .map((column, key) => column.berat)
                      .reduce((a, b) => a + b, 0)
                      .toFixed(1)}
              </>
            )}
          </div> */}
        </div>
      </form>
    );
  }
}

HeadaHancurDataBarang = reduxForm({
  form: "HeadaHancurDataBarang",
  enableReinitialize: true,
  validate: ValidasiPindahBarang,
})(HeadaHancurDataBarang);
export default connect(maptostate, null)(HeadaHancurDataBarang);
